import Papa from 'papaparse'

export async function getData() {
    return await fetch('/data/m1.csv')
        .then((response) => response.text())
        .then((value: string) => Papa.parse<any>(value))
        .then((result: any) => result.data)
}

interface IndicatorValue {
    id: number
    year: string
    area: string
    value: number
}

export interface IndicatorData {
    labels: string[]
    values: number[]
}

export class CriminalData {
    public static values: IndicatorValue[] = []

    public static async fetch() {
        if (this.values.length === 0) {
            let data = await getData()

            let regions: IndicatorValue[] = []
            let republic: IndicatorValue[] = []

            data.slice(1).forEach((item: any) => {
                const value: IndicatorValue = {
                    id: Number(item[2]),
                    year: String(item[1]),
                    area: String(item[3]),
                    value: Number(item[4])
                }
                this.values.push(value)

                const regionFound = regions.find(regionValue => regionValue.id === value.id && regionValue.year === value.year && regionValue.area === value.area.substr(0, 4))
                if (regionFound) {
                    regionFound.value = regionFound.value + value.value
                } else {
                    const regionValue: IndicatorValue = {
                        id: value.id,
                        year: value.year,
                        area: value.area.substr(0, 4),
                        value: value.value
                    }
                    regions.push(regionValue)
                }

                const republicFound = republic.find(republicValue => republicValue.id === value.id && republicValue.year === value.year && republicValue.area === value.area.substr(0, 2))
                if (republicFound) {
                    republicFound.value = republicFound.value + value.value
                } else {
                    const republicValue: IndicatorValue = {
                        id: value.id,
                        year: value.year,
                        area: value.area.substr(0, 2),
                        value: value.value
                    }
                    republic.push(republicValue)
                }
            })

            this.values.push(...regions)
            this.values.push(...republic)
        }
    }

    public static getDataI1Years(): IndicatorData {
        const data: IndicatorData = {labels: [], values: []}
        this.values
            .filter(item => (item.id === 1 && item.area === '19'))
            .map(item => {
                data.labels.push(item.year)
                data.values.push(item.value)
            })
        return data
    }
}
