import {IndicatorData} from "@/shared/loader";

export interface IndicatorChart {
    data: any
    options: any
}

export class CriminalCharts {
    public static settingsI1(data: IndicatorData): IndicatorChart {
        console.log(data)
        return {
            data: {
                labels: data.labels,
                datasets: [
                    {
                        label: ' (.)',
                        fill: false,
                        backgroundColor: '#985959',
                        borderColor: '#FF3D00',
                        pointRadius: 5,
                        data: data.values.map(value => value/1000)
                    }
                ]
            },
            options: {
                responsive: true,
                maintainAspectRatio: false,
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero: true
                        },
                        scaleLabel: {
                            display: true,
                            labelString: ' (.)'
                        }
                    }]
                },
                legend: {
                    display: false,
                    position: "bottom"
                },
                title: {
                    display: true,
                    text: ' '
                },
                events: ['mousemove'],
                onHover: (event: any, chartElement: any) => {
                    event.target.style.cursor = chartElement[0] ? 'pointer' : 'default';
                }
            }
        }
    }

}